.register-page {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(135deg, #e21212, #68b1d6); 
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;

}

.register-page form {
  background: rgba(255, 255, 255, 0.95);
  padding: 1.5rem 2rem;
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  width: 100%;
  max-width: 400px; /* ðŸ‘ˆ smaller width */
}

/* Title */
.register-page h1 {
  font-size: 1.5rem;
  text-align: center;
  color: #2c3e50;
  margin-bottom: 1rem;
}

/* Form labels */
.register-page .ant-form-item-label > label {
  color: #34495e;
  font-weight: 500;
}

/* Inputs */
.register-page input {
  border-radius: 8px;
  padding: 10px;
  border: 1px solid #dcdde1;
  transition: border-color 0.2s ease;
}

.register-page input:focus {
  border-color: #4a69bd;
  box-shadow: 0 0 0 2px rgba(74, 105, 189, 0.2);
}

/* Button */
.register-page .btn-primary {
  background-color: #4a69bd;
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  font-weight: 500;
  transition: background-color 0.3s ease;
  color: white;
}

.register-page .btn-primary:hover {
  background-color: #3b5998;
}

/* Link */
.register-page a {
  color: #4a69bd;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.3s ease;
}

.register-page a:hover {
  color: #2e468c;
}

/* Responsive for smaller screens */
@media screen and (max-width: 480px) {
  .register-page form {
    padding: 1rem 1.5rem;
    max-width: 90%;
  }

  .register-page h1 {
    font-size: 1.3rem;
  }
}



