/* Container and filter section */
.content {
  background-color: #c5daee;
  border-radius: 20px;
  padding: 24px;
  margin: 20px 0;
  box-shadow: 12px;
}

.filters {
  background-color: #b9aec7;
  border-radius: 15px;
  padding: 20px;
  margin: 20px 0;
  box-shadow: 12px;
}

/* Add New button */
.add-new-btn {
  background-color: #eb657b;
  color: white;
  padding: 10px 25px;
  border-radius: 15px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-new-btn:hover {
  opacity: 0.9;
}

/* Flexbox layout for filters */
.filters {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 16px;
  
 
}

/* Filter section spacing */
.filters > div {
  margin-bottom: 16px;
}

/* Table styling without box shadow */
.ant-table-wrapper {
  background-color: #fff;
  border-radius: 20px;
  padding: 16px;
}

/* Rounded pill style for income/expense badge */
.ant-table-cell .ant-tag {
  border-radius: 999px;
  padding: 4px 12px;
  font-weight: bold;
  font-size: 12px;
}

/* Background of page */
body {
  background: linear-gradient(135deg, #fdf6f0 0%, #f4f5ff 100%);
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
}

/* Modal rounded corners */
.ant-modal-content {
  border-radius: 20px !important;
}

/* Edit and Delete icon base styles */
.anticon-edit,
.anticon-delete {
  font-size: 18px;
  cursor: pointer;
  transition: color 0.3s ease;
}

/* Edit icon color */
.anticon-edit {
  color: #083361; /* Bootstrap primary */
}

.anticon-edit:hover {
  color: #043f80; /* Darker blue on hover */
}

/* Delete icon color */
.anticon-delete {
  color: #dc3545; /* Bootstrap danger */
}

.anticon-delete:hover {
  color: #a71d2a; /* Darker red on hover */
}

/* Optional: spacing between icons */
.anticon + .anticon {
  margin-left: 12px;
}
