/* Fullscreen center layout */
.login-page {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(135deg, #e21212, #68b1d6); /* soft gradient */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Login card container */
.login-page .shadow {
  background: rgba(231, 39, 39, 0.9);
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px); /* subtle glassmorphism effect */
  width: 100%;
  max-width: 400px;
}

/* Heading */
.login-page h1 {
  font-size: 1.8rem;
  text-align: center;
  color: #2c3e50;
  margin-bottom: 1.5rem;
}

/* Ant Design form label style */
.login-page .ant-form-item-label > label {
  color: #34495e;
  font-weight: 500;
}

/* Input fields */
.login-page input {
  border-radius: 8px;
  padding: 10px;
  border: 1px solid #dcdde1;
  transition: border-color 0.2s ease;
}

.login-page input:focus {
  border-color: #4a69bd;
  box-shadow: 0 0 0 2px rgba(74, 105, 189, 0.2);
}

/* Primary login button */
.login-page .btn-primary {
  background-color: #4a69bd;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: 500;
  transition: background-color 0.3s ease;
  color: white;
}

.login-page .btn-primary:hover {
  background-color: #3b5998;
}

/* Register link */
.login-page a {
  color: #4a69bd;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.3s ease;
}

.login-page a:hover {
  color: #2e468c;
}

/* Responsive design */
@media screen and (max-width: 480px) {
  .login-page .shadow {
    padding: 1.5rem;
    border-radius: 12px;
  }
}
