* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}





.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}



.switch-icons {
   border: 1px solid rgba(0, 0, 0, 0.678);
   border-radius: 15px;
   padding: 16px 16px;
}

.anticon svg {
    font-size: 20px;
    cursor: pointer;
}


.active-icon {
  color: rgb(125, 90, 221);
}
.inactive-icon {
  color: black;
}






